HDMap.GeocoderSearch = HDMap.Class.extend({
    options: {},

    initialize: function (options) {
        if (typeof options === 'object' && options instanceof HDMap.GeocoderSearchOptions) {
            options = HDMap.setOptions(this, options);
            this._request = {};
        } else {
            throw new Error('Invalid GeocoderSearchOptions object');
        }
    },

    geoEncode:function(address, region) {
        if (typeof address === 'string' && typeof region === 'string') {
            //�ȸ��ݳ���������������������
            var baseUrl = this.options.url;
            var districtUrl = this.options.url + 'v1/geocoder?{address}&{region}';
            var districtRequestUrl = HDMap.Util.serviceUrlTemplate(districtUrl, HDMap.Util.extend({
                address:address,
                region:region
            }));
            var self = this;
            HDMap.Util.ajax(encodeURI(districtRequestUrl), function(data) {
                //��������������������
                if (data !== 'error') {
                    self._processDistrictName(baseUrl, address, data);
                } else {
                    self._result = HDMap.geocoderResult(true, {status:-1, message:'Network Error'});
                }
                self.options.onSearchComplete(self._result);
            });
            return true;
        } else {
            return false;
        }
    },
    _processDistrictName:function(url, address, districtData) {
        //�ж��Ƿ�����һ����������������
        var regionCode = '';
        /*eslint-disable no-unused-vars*/
        var isProvince = false;
        for (var i in districtData.results) {
            if (districtData.results[i].length === 3) {
                //�м���������
                regionCode = districtData.results[i][2].district_code;
            } else if (districtData.results[i].length === 2) {
                //ʡ�ݻ���ֱϽ��
                var districtName = districtData.results[i][1].district_name;
                if (districtName.indexOf('����') >= 0 ||
                    districtName.indexOf('�Ϻ�') >= 0 ||
                    districtName.indexOf('����') >= 0 ||
                    districtName.indexOf('����') >= 0) {
                    isProvince = false;
                } else {
                    isProvince = true;
                }
                regionCode = districtData.results[i][1].district_code;
            } else {
                continue;
            }
            //������ʡ�����о�ֱ���˳�
            break;
        }
        var baseUrl = this.options.url + 'v1/geocoder?{address}&{region}';
        var infoRequestUrl = HDMap.Util.serviceUrlTemplate(baseUrl, HDMap.Util.extend({
            address:address,
            region:regionCode
        }));
        //���ҵ�ǰ�����Ƿ�������������
        if (this._request[infoRequestUrl]) {
            return true;
        }
        this._address = address;
        this._region = regionCode;
        //���ӵ������б���
        this._request[infoRequestUrl] = 'geoEncode';
        this._searchType = 'geoEncode';
        var self = this;
        HDMap.Util.ajax(encodeURI(infoRequestUrl), function(data) {
            //������������ɾ����Ϣ
            delete self._request[infoRequestUrl];
            //������������
            if (data !== 'error') {
                self._result = HDMap.geocoderResult(true, data);
            } else {
                self._result = HDMap.geocoderResult(true, {status:-1, message:'Network Error'});
            }
            self.options.onSearchComplete(self._result);
        });
    },

    geoDecode: function(location) {
        if (location instanceof HDMap.LatLng) {
            //���ڱ����淶Ҫ�󣬲�����������Ϊpage_size��ʽ������ֻ��_�Ժ��ı������д���
            var baseUrl = this.options.url + 'v1/geocoder?{location}';
            var latLonStr = [location.lat, location.lng].join(',');
            var infoRequestUrl = HDMap.Util.serviceUrlTemplate(baseUrl, HDMap.Util.extend({
                location: latLonStr
            }));
            //���ҵ�ǰ�����Ƿ�������������
            if (this._request[infoRequestUrl]) {
                return true;
            }
            this._location = location;
            //���ӵ������б���
            this._request[infoRequestUrl] = 'geoDecode';
            this._searchType = 'geoDecode';
            var self = this;
            HDMap.Util.ajax(encodeURI(infoRequestUrl), function (data) {
                //������������ɾ����Ϣ
                delete self._request[infoRequestUrl];
                //������������
                if (data !== 'error') {
                    self._result = HDMap.geocoderResult(false, data);
                } else {
                    self._result = HDMap.geocoderResult(false, {status : -1, message : 'Network Error'});
                }
                self.options.onSearchComplete(self._result);
            });
            return true;
        } else {
            return false;
        }
    }
});


